













import { Component, Prop, Vue } from 'vue-property-decorator';
import eCharts from 'vue-echarts'

@Component({
  components: {
    chart: eCharts,
  }
})
export default class Map extends Vue {
  @Prop({ default: () => {} }) readonly option: Array<any>
  private theme = require('../theme/index.json')

  handleClick(e) {
    this.$emit('click', e)
  }
}
